import React from 'react';
import Img from 'gatsby-image';
import fluidImgProps from '../common/fluidImgProps';

export default class ImageSequence extends React.PureComponent {
  timing = 5000;
  imageSequenceInterval;

  state = {
    activeImage: 0,
  };

  componentDidMount() {
    this.imageSequenceAnimate();
  }

  componentWillUnmount() {
    clearInterval(this.imageSequenceInterval);
  }

  imageSequenceAnimate() {
    const images = [...this.props.images];

    if (images.length > 1) {
      setTimeout(() => {
        this.imageSequenceInterval = this.createSequenceInterval(images);
      }, (this.props.col * this.timing) / 2); // to offset every even contact image
    }
  }

  createSequenceInterval(images) {
    return setInterval(() => {
      const active = this.state.activeImage;

      let newActive = active === images.length - 1 ? 0 : active + 1;

      this.setState({ activeImage: newActive });
    }, this.timing);
  }

  render() {
    const { images, col } = this.props;

    return (
      <div className="contact-imageSequence">
        {images !== undefined
          ? images.map((officeImg, j) => {
              const props = fluidImgProps(officeImg.asset);

              let active = this.state.activeImage === j ? '-active' : '';

              return (
                <Img
                  key={`sequence ${col} image ${j}`}
                  className={`contact-imageSequence-image ${active}`}
                  style={{ position: 'absolute' }}
                  {...props}
                />
              );
            })
          : null}
      </div>
    );
  }
}
