import React from 'react';
import { graphql } from 'gatsby';
import BlockContent from '@sanity/block-content-to-react';

import ImageSequence from '../components/contact/ImageSequence';

const addTargetBlanks = url => {
  const props = {
    href: url
  }

  if(url.match(/http/g)){
    props.target = "_blank";
  } else if(url.match(/@/g)){
    props.href = `mailto:${url}`;
  }

  return props;
};

const link = props => (
  <a {...addTargetBlanks(props.mark.href)}>
    {props.children}
  </a>
);

const Contact = ({ data }) => {
  const entries = data.sanityContact.contact;
  const rawDetails = data.sanityContact._rawContact.map(raw => raw.details);

  return (
    <section className="contact">
      <div className="contact-container">
        {entries.map((mod, i) => (
          <div className="contact-column" key={mod.title}>
            <ImageSequence images={mod.image} col={i} />

            <div className="contact-text">
              <h2 className="contact-city">{mod.title}</h2>
              <div className="contact-details">
                <BlockContent blocks={rawDetails[i]} serializers={{marks: {link}}}/>
              </div>
            </div>

            <div className="contact-links">
              {mod.contactLinks.map(link => (
                <div className="contact-links-row" key={link.title}>
                  <p className="contact-links-type">{link.title}</p>
                  <span>
                    <a {...addTargetBlanks(link.link)}>{link.text}</a>
                  </span>
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Contact;

export const query = graphql`
  {
    sanityContact {
      contact {
        title
        image {
          asset {
            fluid(maxWidth: 650) {
              src
              sizes
              srcSet
              srcWebp
              srcSetWebp
              aspectRatio
            }

            metadata{
              palette{
                dominant{
                  background
                }
              }
            }
          }
        }
        contactLinks {
          title
          text
          link
        }
      }
      _rawContact
    }
  }
`;
